/* Общие стили */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  transition: background 0.3s ease;
}

/* Темы */
body.light {
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
}

body.dark {
  background: linear-gradient(135deg, #1a1a2e 0%, #16213e 100%);
}

.app {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  transition: all 0.3s ease;
}

.app.light {
  background-color: #f8fafc;
  color: #1a202c;
}

.app.dark {
  background-color: #1a202c;
  color: #f7fafc;
}

/* Заголовок */
.header {
  position: sticky;
  top: 0;
  z-index: 1000;
  background: inherit;
  border-bottom: 1px solid;
}

.app.light .header {
  border-bottom-color: #e2e8f0;
  background: rgba(248, 250, 252, 0.95);
  backdrop-filter: blur(10px);
}

.app.dark .header {
  border-bottom-color: #2d3748;
  background: rgba(26, 32, 44, 0.95);
  backdrop-filter: blur(10px);
}

/* Навигация */
.main-nav {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 1rem 2rem;
  max-width: 1200px;
  margin: 0 auto;
  width: 100%;
  box-sizing: border-box;
}

.nav-brand {
  display: flex;
  align-items: center;
}

.brand-link {
  display: flex;
  align-items: center;
  text-decoration: none;
  color: inherit;
  font-weight: 700;
  font-size: 1.25rem;
  transition: transform 0.2s ease;
}

.brand-link:hover {
  transform: scale(1.05);
}

.brand-icon {
  margin-right: 0.5rem;
  font-size: 1.5rem;
}

.brand-text {
  background: linear-gradient(45deg, #4299e1, #38b2ac);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.nav-links {
  display: flex;
  align-items: center;
  gap: 2rem;
}

.nav-link {
  text-decoration: none;
  color: inherit;
  padding: 0.5rem 1rem;
  border-radius: 0.5rem;
  transition: all 0.2s ease;
  font-weight: 500;
  position: relative;
}

.nav-link:hover {
  transform: translateY(-1px);
}

.app.light .nav-link:hover {
  background-color: #edf2f7;
  color: #2d3748;
}

.app.dark .nav-link:hover {
  background-color: #2d3748;
  color: #f7fafc;
}

/* Акцент активной ссылки: белый текст для лучшей читаемости */
.nav-link.active {
  font-weight: 700;
}
.app.light .nav-link.active {
  background-color: #4299e1;
  color: #fff;
  box-shadow: 0 6px 16px rgba(66,153,225,0.35);
}
.app.dark .nav-link.active {
  background-color: #38b2ac;
  color: #fff;
  box-shadow: 0 6px 16px rgba(56,178,172,0.35);
}

/* Пользовательская секция */
.nav-user {
  display: flex;
  align-items: center;
  gap: 1rem;
}

.theme-toggle {
  background: none;
  border: 2px solid;
  padding: 0.5rem;
  border-radius: 50%;
  cursor: pointer;
  font-size: 1.2rem;
  transition: all 0.2s ease;
  width: 40px;
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.app.light .theme-toggle {
  border-color: #cbd5e0;
  color: #4a5568;
}

.app.dark .theme-toggle {
  border-color: #4a5568;
  color: #a0aec0;
}

.theme-toggle:hover {
  transform: scale(1.1) rotate(15deg);
}

/* Улучшенные hover-анимации для ссылок и кнопок */
.nav-link, .auth-link, .btn, .theme-toggle, .menu-toggle {
  transition: background-color 0.2s ease, color 0.2s ease, transform 0.2s ease, box-shadow 0.2s ease;
}

/* Бургер-меню справа */
.menu-toggle {
  background: none;
  border: 2px solid;
  border-radius: 10px;
  padding: 6px 8px;
  cursor: pointer;
  display: inline-flex;
  flex-direction: column;
  gap: 4px;
  align-items: center;
  justify-content: center;
}
.app.light .menu-toggle { border-color: #cbd5e0; }
.app.dark .menu-toggle { border-color: #4a5568; }
.menu-toggle .bar {
  width: 18px;
  height: 2px;
  background: currentColor;
  border-radius: 2px;
}
.menu-toggle:hover { transform: translateY(-1px); }

/* Выпадающее меню пользователя */
.user-dropdown {
  position: absolute;
  right: 0;
  top: calc(100% + 10px);
  min-width: 220px;
  background: var(--dropdown-bg, #fff);
  color: var(--dropdown-fg, #1a1a1a);
  border-radius: 12px;
  box-shadow: 0 12px 28px rgba(0,0,0,0.18);
  border: 1px solid rgba(0,0,0,0.06);
  padding: 8px;
  opacity: 0;
  transform: translateY(-8px) scale(0.98);
  pointer-events: none;
  transition: opacity 0.15s ease, transform 0.15s ease;
}
.app.dark .user-dropdown {
  --dropdown-bg: #1f2a48;
  --dropdown-fg: #e0e7ef;
  border: 1px solid #2a3441;
}
.user-dropdown.open {
  opacity: 1;
  transform: translateY(0) scale(1);
  pointer-events: auto;
}
.user-menu { position: relative; }
.user-dropdown-item {
  display: block;
  text-decoration: none;
  color: inherit;
  padding: 10px 12px;
  border-radius: 8px;
  font-weight: 600;
}
.user-dropdown-item:hover { background: rgba(0,0,0,0.06); }
.app.dark .user-dropdown-item:hover { background: rgba(255,255,255,0.06); }
.user-dropdown-item.danger { color: #e53e3e; }

/* Пользовательское меню */
.user-menu {
  display: flex;
  align-items: center;
  gap: 1rem;
}

.user-info {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  padding: 0.5rem 1rem;
  border-radius: 0.5rem;
  background: linear-gradient(45deg, #4299e1, #38b2ac);
  color: white;
  font-weight: 500;
}

.profile-avatar {
  font-size: 1.2rem;
}

.profile-name {
  font-weight: 600;
}

.user-balance {
  font-size: 0.9rem;
  opacity: 0.9;
  background: rgba(255, 255, 255, 0.2);
  padding: 0.25rem 0.5rem;
  border-radius: 0.25rem;
}

.logout-btn {
  background: #e53e3e;
  color: white;
  border: none;
  padding: 0.5rem 1rem;
  border-radius: 0.5rem;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.2s ease;
}

.logout-btn:hover {
  background: #c53030;
  transform: translateY(-1px);
}

/* Ссылки аутентификации */
.auth-links {
  display: flex;
  align-items: center;
  gap: 0.5rem;
}

.auth-link {
  text-decoration: none;
  color: inherit;
  padding: 0.5rem 1rem;
  border-radius: 0.5rem;
  font-weight: 500;
  transition: all 0.2s ease;
  border: 2px solid transparent;
}

.auth-link:hover {
  transform: translateY(-1px);
}

.app.light .auth-link:hover {
  background-color: #edf2f7;
  color: #2d3748;
}

.app.dark .auth-link:hover {
  background-color: #2d3748;
  color: #f7fafc;
}

.auth-link.register {
  border: 2px solid;
  font-weight: 600;
}

.app.light .auth-link.register {
  border-color: #4299e1;
  color: #4299e1;
}

.app.dark .auth-link.register {
  border-color: #38b2ac;
  color: #38b2ac;
}

.app.light .auth-link.register:hover {
  background-color: #4299e1;
  color: white;
}

.app.dark .auth-link.register:hover {
  background-color: #38b2ac;
  color: white;
}

/* Основной контент */
.main-content {
  flex: 1;
  display: flex;
  flex-direction: column;
}

/* Кнопки */
.btn {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  padding: 12px 24px;
  border: none;
  border-radius: 8px;
  font-weight: 600;
  font-size: 14px;
  text-decoration: none;
  cursor: pointer;
  transition: all 0.3s ease;
  position: relative;
  overflow: hidden;
}

.btn::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.2), transparent);
  transition: left 0.5s;
}

.btn:hover::before {
  left: 100%;
}

.btn-primary {
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  color: white;
  box-shadow: 0 4px 15px rgba(102, 126, 234, 0.4);
}

.btn-primary:hover {
  transform: translateY(-2px);
  box-shadow: 0 6px 20px rgba(102, 126, 234, 0.6);
}

.btn-secondary {
  background: rgba(255, 255, 255, 0.9);
  color: #667eea;
  border: 2px solid #667eea;
}

.app.dark .btn-secondary {
  background: rgba(26, 34, 62, 0.9);
  color: #4cafef;
  border: 2px solid #4cafef;
}

.btn-secondary:hover {
  background: #667eea;
  color: white;
  transform: translateY(-2px);
}

.app.dark .btn-secondary:hover {
  background: #4cafef;
}

.btn-large {
  padding: 16px 32px;
  font-size: 16px;
}

/* Главная страница */
.home-page {
  min-height: 100vh;
}

/* Page background utility
   Replace URLs below with your images (e.g. /images/home-bg.jpg). */
.page-bg {
  min-height: 100vh; /* ensure it fills viewport */
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  position: relative;
  overflow: hidden;
  z-index: 0; /* stacking base for pseudo elements */
}

/* Background image moved to ::after so we can invert it only when needed */
.page-bg::after {
  content: "";
  /* make the background cover the entire viewport regardless of the element's width
     use fixed so the image spans the full window and doesn't get clipped by container widths */
  position: fixed;
  inset: 0;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  /* per-page image via custom property (set below) */
  background-image: var(--page-bg-image, none);
  z-index: -2; /* behind the overlay and content */
  transition: filter 0.25s ease, opacity 0.25s ease;
  pointer-events: none;
}

/* subtle overlay: 7% to reduce pattern prominence */
.page-bg::before {
  content: "";
  /* overlay also fixed so it lines up with the fixed background image */
  position: fixed;
  inset: 0;
  pointer-events: none;
  background: rgba(255,255,255,0.07); /* light overlay by default */
  transition: background 0.3s;
  z-index: -1; /* above image but behind content */
}

/* Ensure page content sits above overlay */
.page-bg > * {
  position: relative;
  z-index: 2;
}

/* Dark theme: darker image + dark overlay (7%) */
.app.dark .page-bg::before {
  background: rgba(0,0,0,0.07);
}

/* Light theme: invert only the background image (do not affect content) */
.app.light .page-bg::after {
  /* Преобразуем тёмное фоновое изображение в светлые, пастельные тона:
     - invert для базового перехода,
     - hue-rotate и reduce saturation для корректировки оттенка,
     - slight brightness/contrast boost для читаемости,
     - уменьшаем opacity, чтобы UI оставался на переднем плане.
  */
  filter: invert(1) hue-rotate(160deg) saturate(0.9) brightness(1.08) contrast(1.03);
  opacity: 0.5;
  transition: filter 0.25s ease, opacity 0.25s ease;
}

/* Per-page images — now assigned to CSS variable used by ::after */
.home-page.page-bg { --page-bg-image: url('./images/home-bg.png'); }
.trade-page.page-bg { --page-bg-image: url('./images/trade-bg.png'); }
.profile-page.page-bg { --page-bg-image: url('./images/profile-bg.png'); }
.wallet-page.page-bg { --page-bg-image: url('./images/wallet-bg.png'); }

/* If you need a darker overlay on hero pages, tune these values */
.home-page.page-bg::before { background: rgba(0,0,0,0.07); }
.trade-page.page-bg::before { background: rgba(0,0,0,0.07); }


.hero-section {
  text-align: center;
  padding: 80px 20px;
  background: linear-gradient(135deg, rgba(255, 255, 255, 0.1) 0%, rgba(255, 255, 255, 0.05) 100%);
  border-radius: 20px;
  margin-bottom: 60px;
  backdrop-filter: blur(10px);
  border: 1px solid rgba(255, 255, 255, 0.2);
  transition: all 0.3s ease;
}

.app.dark .hero-section {
  background: linear-gradient(135deg, rgba(76, 175, 239, 0.1) 0%, rgba(76, 175, 239, 0.05) 100%);
  border: 1px solid rgba(76, 175, 239, 0.2);
}

.app.light .hero-section {
  /* half the blur for a crisper UI on light backgrounds */
  backdrop-filter: blur(5px);
  -webkit-backdrop-filter: blur(5px);

  /* slightly darker, low-contrast tint to make hero text pop on light pages */
  background: linear-gradient(135deg,
    rgba(2,6,23,0.04) 0%,
    rgba(2,6,23,0.02) 100%);

  /* subtle darker border */
  border: 1px solid rgba(2,6,23,0.04);
}

.hero-title {
  font-size: 3.5rem;
  font-weight: 800;
  margin-bottom: 20px;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.app.dark .hero-title {
  background: linear-gradient(135deg, #4cafef 0%, #2196f3 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.highlight {
  color: #667eea;
  -webkit-text-fill-color: #667eea;
}

.app.dark .highlight {
  color: #4cafef;
  -webkit-text-fill-color: #4cafef;
}

.hero-subtitle {
  font-size: 1.3rem;
  color: #666;
  margin-bottom: 40px;
  max-width: 600px;
  margin-left: auto;
  margin-right: auto;
}

.app.dark .hero-subtitle {
  color: #b0b8c8;
}

.hero-buttons {
  display: flex;
  gap: 20px;
  justify-content: center;
  margin-bottom: 60px;
}

.hero-stats {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 30px;
  max-width: 800px;
  margin: 0 auto;
}

.stat-card {
  background: rgba(255, 255, 255, 0.9);
  padding: 30px 20px;
  border-radius: 15px;
  text-align: center;
  box-shadow: 0 8px 25px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s ease;
}

.app.dark .stat-card {
  background: rgba(26, 34, 62, 0.9);
  box-shadow: 0 8px 25px rgba(0, 0, 0, 0.3);
}

.stat-card:hover {
  transform: translateY(-5px);
}

.stat-number {
  font-size: 2.5rem;
  font-weight: 800;
  color: #667eea;
  margin-bottom: 10px;
}

.app.dark .stat-number {
  color: #4cafef;
}

.stat-label {
  color: #666;
  font-weight: 500;
}

.app.dark .stat-label {
  color: #b0b8c8;
}

/* Banner carousel */
.banner-section {
  padding: 40px 12px;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 14px;
  box-sizing: border-box;
}

/* container для карусели — раньше ограничивался max-width (центрированный).
   Теперь делаем «breakout» чтобы карусель могла занимать full viewport width. */
.banner-shell {
  /* break out of centered page container and span the full viewport */
  width: 100vw;
  max-width: none;
  position: relative;
  margin-left: calc(50% - 50vw); /* shift left so element spans edge-to-edge */
  margin-right: calc(50% - 50vw);
  display: flex;
  align-items: center;
  justify-content: center;
  box-sizing: border-box;
  padding: 0 36px; /* сохраняем горизонтальные «грабберы» для отступа внутри viewport */
}

/* viewport for the carousel */
.banner-carousel {
  width: 100%;
  overflow-x: auto;
  -webkit-overflow-scrolling: touch;
  scroll-snap-type: x mandatory;
  scrollbar-width: none;
  -ms-overflow-style: none;
  padding: 12px 36px; /* room for centering */

  /* плавная анимация перелистывания */
  scroll-behavior: smooth;

  /* ensure the centered snap has padding so items can be centered properly */
  scroll-padding-inline: 36px;
}

/* track holds items in a row */
.banner-track {
  display: flex;
  gap: 30px; /* increased gap between banners */
  align-items: center; /* center children vertically to avoid layout jumps */
  box-sizing: border-box;
  padding-bottom: 4px;
}

/* each banner — теперь занимает всю ширину окна (edge-to-edge).
   Медиаправило (max-width:760px) ниже продолжает переопределять поведение для маленьких экранов. */
.banner-item {
  flex: 0 0 800px;       /* ширина карточки = ширине viewport */
  max-width: 100vw;      /* не ограничиваем дополнительным max-width */
  height: 500px;
  scroll-snap-align: center;
  border-radius: 16px;
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
  padding: 22px 24px;
  box-sizing: border-box;
  color: #fff;
  cursor: pointer;
  transition: transform 420ms cubic-bezier(.2,.9,.2,1), box-shadow 420ms, opacity 220ms;
  box-shadow: 0 10px 30px rgba(0,0,0,0.18);
  border: 1px solid rgba(255,255,255,0.06);
  position: relative;
  overflow: hidden;
  background: transparent;
  will-change: transform, opacity, box-shadow;
  scroll-snap-stop: always;
  /* Центрируем внутреннее содержимое визуально (если нужно) */
  margin: 0 auto;
}

/* фон-изображение под текстом, размытие */
.banner-item::before{
  content: "";
  position: absolute;
  inset: 0;
  /* now supports separate images per theme (dark by default) */
  background-image: var(--bg-img-dark, var(--bg-img, none));
  background-size: cover;
  background-position: center;
  filter: blur(8px);
  transform: scale(1.03);
  opacity: 0.6;
  z-index: 0;
  transition: filter 420ms ease, opacity 420ms ease, transform 420ms ease, background-image 250ms ease;
  border-radius: inherit;
}

/* light theme: prefer light variant if provided */
.app.light .banner-item::before {
  background-image: var(--bg-img-light, var(--bg-img-dark, var(--bg-img, none)));
}

/* мягкий градиент поверх изображения, но под текстом */
.banner-item::after{
  content: "";
  position: absolute;
  inset: 0;
  background: var(--grad, linear-gradient(135deg, rgba(0,0,0,0.25), rgba(0,0,0,0.35)));
  opacity: 0.7;
  z-index: 1;
  border-radius: inherit;
  transition: background 420ms ease, opacity 420ms ease;
}

/* content above image+gradient */
.banner-content {
  max-width: 90%;
  display: flex;
  flex-direction: column;
  gap: 8px;
  align-items: flex-start;
  position: relative;
  z-index: 2;
  padding-top: 6px;
}

/* более спокойные, менее контрастные заголовки/подзаголовки */
.banner-title {
  font-size: 2.4rem;
  font-weight: 800;
  margin: 0;
  color: inherit;
  text-shadow: 0 10px 30px rgba(0,0,0,0.28);
  opacity: 0.90;
}

.app.dark .banner-sub {
  margin: 0;
  font-size: 1.5rem; /* was 1.5rem, doubled */
  opacity: 0.35;
  font-weight: 600;
  color: rgba(255, 255, 255, 0.95);
}

/* Light theme: make banner title gradient (like other headings) and subtitle dark-gray */
.app.light .banner-title {
  /* same gradient style as .hero-title / .page-title for visual consistency */
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  color: inherit; /* fallback */
  /* soften shadow for light background */
  text-shadow: 0 6px 18px rgba(2,6,23,0.06);
}

.app.light .banner-sub {
  color: rgba(171, 171, 171, 0.95); /* dark gray for subtitle in light theme */
  font-size: 1.5rem;
  font-weight: 600;
  -webkit-text-fill-color: #374151;
  opacity: 0.73;
}

/* active — чуть сильнее выделение и glow вокруг */
.banner-item.active {
  transform: scale(1.036);
  box-shadow: 0 30px 72px rgba(0,0,0,0.32);
  z-index: 5;
}
.banner-item.active::before {
  filter: blur(6px);
  opacity: 0.7;
  transform: scale(1.02);
}
.banner-item.active::after {
  opacity: 0.78;
}

/* Hover: лёгкое приподнимание и подсветка под карточкой */
.banner-item:hover,
.banner-item:focus {
  transform: translateY(-8px) scale(1.012);
  /* основная тень + тонкая цветная подсветка снизу для акцента */
  box-shadow:
    0 30px 64px rgba(2,6,23,0.22),   /* глубинная тень */
    0 12px 30px rgba(102,126,234,0.12); /* мягкая синяя подсветка */
  z-index: 6;
}

/* Light theme: чуть темнее тон подсветки (лучше контрастирует на светлом фоне) */
.app.light .banner-item:hover,
.app.light .banner-item:focus {
  transform: translateY(-8px) scale(1.01);
  box-shadow:
    0 26px 56px rgba(2,6,23,0.08),   /* более лёгкая глубинная тень */
    0 10px 28px rgba(102,126,234,0.10); /* мягкая синяя подсветка */
  border-color: rgba(2,6,23,0.06);
}

/* Dark theme: холодная бирюзовая подсветка для контраста */
.app.dark .banner-item:hover,
.app.dark .banner-item:focus {
  transform: translateY(-8px) scale(1.014);
  box-shadow:
    0 32px 72px rgba(0,0,0,0.45),
    0 14px 34px rgba(76,175,239,0.10); /* бирюзовая подсветка */
  border-color: rgba(255,255,255,0.06);
}

/* Мягкое снижение эффекта на небольших экранах, чтобы не перекрывало интерфейс */
@media (max-width: 760px) {
  .banner-item:hover,
  .banner-item:focus {
    transform: translateY(-6px) scale(1.008);
    box-shadow:
      0 18px 34px rgba(0,0,0,0.18),
      0 8px 18px rgba(0,0,0,0.08);
    z-index: 4;
  }
}

/* active — чуть сильнее выделение и glow вокруг */
.banner-item.active {
  transform: scale(1.036);
  box-shadow: 0 30px 72px rgba(0,0,0,0.32);
  z-index: 5;
}
.banner-item.active::before {
  filter: blur(6px);
  opacity: 0.7;
  transform: scale(1.02);
}
.banner-item.active::after {
  opacity: 0.78;
}

/* убираем кнопку CTA (она больше не отображается) */
/* .banner-cta { ... } */ /* removed intentionally */

/* arrows */
.banner-arrow {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  width: 48px;
  height: 48px;
  border-radius: 12px;
  border: none;
  background: rgba(255,255,255,0.12);
  color: white;
  font-size: 26px;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  z-index: 10;
  transition: background 180ms, transform 160ms;
  backdrop-filter: blur(6px);
}

/* Light theme: dark-gray background for banner navigation arrows */
.app.light .banner-arrow {
  background: #a7a9ab; /* dark gray (slate) */
  color: #ffffff;
  box-shadow: 0 6px 18px rgba(2,6,23,0.06);
  backdrop-filter: blur(6px);
}

/* Slightly lighter on hover for visual feedback in light theme */
.app.light .banner-arrow:hover {
  transform: translateY(-50%) scale(1.05);
  background: #4b5563; /* lighter slate */
}

.banner-arrow.left { left: 6px; }
.banner-arrow.right { right: 6px; }
.banner-arrow:hover { transform: translateY(-50%) scale(1.05); background: rgba(255,255,255,0.18); }

/* dots */
.banner-dots {
  display: flex;
  gap: 8px;
  align-items: center;
  justify-content: center;
}
.banner-dots .dot {
  width: 10px;
  height: 10px;
  border-radius: 999px;
  background: rgba(0,0,0,0.18);
  border: 1px solid rgba(255,255,255,0.12);
  cursor: pointer;
  transition: transform 160ms, background 160ms, opacity 160ms;
}
.banner-dots .dot.active {
  background: #fff;
  transform: scale(1.15);
  box-shadow: 0 6px 18px rgba(0,0,0,0.12);
}

/* theme adjustments */
