/* Modern block-style notifications */
.notifications-list {
  display: flex;
  flex-direction: column;
  gap: 12px;
}
.notification-card {
  background: var(--card);
  border-radius: 12px;
  box-shadow: 0 8px 24px rgba(2,6,23,0.08);
  padding: 16px 18px;
  display: flex;
  gap: 12px;
  align-items: flex-start;
  border: 1px solid rgba(0,0,0,0.04);
}
.notification-card .meta {
  min-width: 92px;
  font-size: 12px;
  color: var(--muted);
}
.notification-card .body {
  flex: 1;
}
.notification-card .actions {
  display: flex;
  gap: 8px;
  align-items: center;
}
.app.dark .notification-card { box-shadow: 0 8px 24px rgba(0,0,0,0.36); border: 1px solid rgba(255,255,255,0.04); }

/* Notifications dropdown wrapper with open/close animation */
.notifications-dropdown {
  width: 380px;
  border-radius: 10px;
  box-shadow: 0 12px 40px rgba(2,6,23,0.12);
  background: transparent; /* default hidden state remains transparent */
  overflow: hidden;
  transform-origin: top right;
  transition: opacity 220ms ease, transform 200ms ease, max-height 260ms ease;
  opacity: 0;
  pointer-events: none;
  max-height: 0;
  /* position offset so the dropdown doesn't overlap the header or the bell button */
  margin-top: 10px;
}
.notifications-dropdown.open {
  opacity: 1;
  pointer-events: auto;
  transform: translateY(0) scale(1);
  max-height: 420px; /* allow content to expand */
  background: var(--card);
  border: 1px solid rgba(0,0,0,0.04);
}
.notifications-dropdown.closed {
  opacity: 0;
  transform: translateY(-8px) scale(0.98);
  max-height: 0;
}

@keyframes popIn {
  from { transform: translateY(-8px) scale(0.98); opacity: 0; }
  to { transform: translateY(0) scale(1); opacity: 1; }
}

/* Copy button animated states */
.copy-btn { position: relative; display: inline-flex; align-items: center; justify-content: center; }
.copy-btn .copy-label, .copy-btn .copy-check {
  display: inline-block;
  transition: opacity 180ms ease, transform 200ms ease;
}
.copy-btn .copy-check { opacity: 0; transform: scale(0.6); }
.copy-btn.copied .copy-label { opacity: 0; transform: translateY(-6px); }
.copy-btn.copied .copy-check { opacity: 1; transform: scale(1); }

