/* Футер */
.footer {
  margin-top: auto;
  padding: 3rem 2rem 1rem;
  border-top: 1px solid;
}

.app.light .footer {
  background-color: #f7fafc;
  border-top-color: #e2e8f0;
}

.app.dark .footer {
  background-color: #2d3748;
  border-top-color: #4a5568;
}

.footer-content {
  max-width: 1200px;
  margin: 0 auto;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 2rem;
}

.footer-section h4 {
  margin-bottom: 1rem;
  font-weight: 600;
  color: inherit;
}

.footer-section ul {
  list-style: none;
  padding: 0;
}

.footer-section ul li {
