body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

/* Theme variables */
:root {
  --bg: #ffffff;
  --card: #ffffff;
  --muted: #666666;
  --accent: #1976d2;
  --text: #0b1220;
  --input-bg: #f5f7fb;
  --notice-bg: #fff6f6;
  --notice-border: #ffd7d7;
}

body.dark {
  --bg: #071226;
  --card: #0f1724;
  --muted: #98a0b6;
  --accent: #4fb3ff;
  --text: #e8eef9;
  --input-bg: #0b1320;
  --notice-bg: #0e1b26;
  --notice-border: rgba(255,255,255,0.04);
}

/* Ensure page background always covers viewport */
.page-bg {
  min-height: 100vh;
  background: radial-gradient(ellipse at top left, rgba(255,255,255,0.02), transparent 25%), var(--bg);
  color: var(--text);
}

.profile-header .page-title {
  margin: 0 0 6px 0;
  color: var(--text);
}

.notice {
  padding: 12px 14px;
  border-radius: 10px;
  margin: 8px 0 12px 0;
}
.notice.success { background: rgba(75, 181, 67, 0.12); border: 1px solid rgba(75,181,67,0.12); }
.notice.error { background: rgba(244,67,54,0.08); border: 1px solid rgba(244,67,54,0.12); }

.form-input {
  padding: 12px 14px;
  border-radius: 8px;
  border: 1px solid rgba(255,255,255,0.04);
  background: var(--input-bg);
  color: var(--text);
  width: 100%;
}

.btn { padding: 10px 14px; border-radius: 8px; border: none; cursor: pointer; }
.btn-primary { background: linear-gradient(180deg, #6c5ce7, #4b35c6); color: #fff; box-shadow: 0 6px 18px rgba(76, 43, 146, 0.25); }
.btn-secondary { background: transparent; color: var(--text); border: 1px solid rgba(255,255,255,0.06); }

.kyc-notice {
  background: var(--notice-bg);
  border: 1px solid var(--notice-border);
  color: var(--text);
}

.kyc-cards { display: grid; grid-template-columns: repeat(3, 1fr); gap: 12px; }
.kyc-card {
  background: var(--card);
  border: 1px dashed rgba(255,255,255,0.06);
  padding: 14px;
  border-radius: 8px;
  text-align: center;
  min-height: 140px;
  display: flex; flex-direction: column; justify-content: space-between;
}
.kyc-card img { max-width: 100%; max-height: 120px; border-radius: 6px; }


code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
